import { Component, Input, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ChartType, ChartOptions } from 'chart.js';
import { SingleDataSet, Label} from 'ng2-charts';
import { retryWhen, take, delayWhen} from 'rxjs/operators'
import { timer } from 'rxjs';

@Component({
  selector: 'app-blitz',
  templateUrl: './blitz.component.html',
  styleUrls: ['./blitz.component.css']
})
export class BlitzComponent implements OnInit {
  @Input() search: any;
  
  constructor(
    private http : HttpClient
  ) { }


  public pieChartOptions: ChartOptions = {
    responsive: true,
  };
  public pieChartLabels: Label[] = [['WIN'], ['LOSS'],['DRAW']];
  public pieChartData: SingleDataSet = [];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [];
  public chartColors: Array<any> = [
    { // all colors in order
      backgroundColor: ['#98ba6f', '#c0070b','#cccccc'] 
    }
  ]

  elo:any;
  loading:any;

  ngOnInit(): void {
  }
  ngOnChanges(){
    this.start();
    this.pieChartData = [];
    this.elo="";
    this.loading = false;
  }
  myType = 'PieChart';
  start(){
    let query = 'https://api.chess.com/pub/player/'+this.search
    this.http.get(query+"/stats").pipe(
      retryWhen(errors => {
          return errors
                  .pipe(
                      delayWhen(() => timer(2000)),
                      take(3)
                  );
      } )
  )
    .subscribe((response:any)=>{
        if(response.chess_blitz !== undefined){
          this.elo=response.chess_blitz.last.rating;
          this.pieChartData = [parseInt(response.chess_blitz.record.win),parseInt(response.chess_blitz.record.loss),parseInt(response.chess_blitz.record.draw)]
          this.loading = true;
        }else{
          this.elo="No records available";
        }
      })
  }
}
